body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #f1dae8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 15px;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 15px;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #1AAC28;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 15px;
}
.boton-verde:hover {
  background-color: #179623;
  cursor: pointer;
}

.boton-accion {
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 4rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  width: fit-content;
  border: 2px solid #F9511F;
  border-radius: 20px;
  transition: 0.5s;
}
.boton-accion:hover {
  background-color: #F9511F;
}

.no-webp .inicio {
  background-image: url(../img/donacion.jpg);
}

.webp .inicio {
  background-image: url(../img/donacion.webp);
}

.header {
  background-color: #850069;
  padding: 0.5rem 5%;
}
.header .logo1 {
  padding: 1px;
}
.header .logo {
  width: 100%;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: 0.5s;
}
.header .logo:hover {
  border: 2px solid #cc459a;
  box-shadow: 0 0 10px #cc459a, 0 0 40px #cc459a, 0 0 70px #cc459a;
}

.inicio .overlay {
  position: absolute;
  background: #5500ff;
  background: linear-gradient(270deg, rgba(85, 0, 255, 0.3) 0%, rgba(255, 0, 229, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenedor.inicio {
  background-position: center center;
  background-size: cover;
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  width: 100%;
  max-width: 100%;
  margin: 0 0 0 0;
  overflow: hidden;
}
.contenedor.inicio h1 {
  color: #FFFFFF;
  margin: 0 0 2.5rem 0;
}

.contenido-imagen {
  border-radius: 5px solid #FFFFFF;
  padding: 20px;
  width: 63rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.footer {
  background-color: #000;
}
@media (min-width: 768px) {
  .footer .navegacion-footer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.footer .navegacion-footer a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .footer .navegacion-footer a {
    margin-right: 3rem;
  }
  .footer .navegacion-footer a:last-of-type {
    margin-top: 0;
  }
}
.footer .navegacion-footer a:hover {
  color: #FFFFFF;
  text-shadow: 0 0 10px #FFFFFF, 0 0 40px #FFFFFF, 0 0 70px #FFFFFF;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 0rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

#check {
  display: none;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
@media (max-width: 769px) {
  .navegacion {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 0;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(50px);
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    overflow: hidden;
    transition: 0.3s ease;
  }
  .navegacion a {
    transform: translateY(-200px);
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
  }
  .navegacion a:last-of-type {
    margin-top: 0;
  }
}
.navegacion a:hover {
  color: #FFFFFF;
  text-shadow: 0 0 10px #FFFFFF, 0 0 40px #FFFFFF, 0 0 70px #FFFFFF;
}

.icons {
  display: inline-flex;
  font-size: 5rem;
  color: #FFFFFF;
  cursor: pointer;
  position: absolute;
  right: 5%;
}
@media (min-width: 768px) {
  .icons {
    display: none;
  }
}

#check:checked ~ .navegacion {
  height: 17.7rem;
}

#check:checked ~ .navegacion a {
  transform: translateY(10px);
  transition-delay: calc(0.1s * var(--i));
}

#close-icon {
  display: none;
}

#check:checked ~ .icons #menu-icon {
  display: none;
}

#check:checked ~ .icons #close-icon {
  display: block;
}

.no-webp .inicio {
  background-image: url(../img/donacion.jpg) no-repeat;
}

.webp .inicio {
  background-image: url(../img/donacion.webp) no-repeat;
}

.header-base h1 {
