}

.barra-base {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.5rem 10%;
  display: flex;
  justify-content: space-between;
  z-index: 100;
}
.barra-base::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(133, 2, 255, 0.7);
  backdrop-filter: blur(10px);
  z-index: -1;
}

.inicio .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(85, 0, 255, 0.3) 0%, rgba(255, 0, 229, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenedor.inicio {
  background-position: center center;
  background-size: cover;
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  width: 100%;
  max-width: 100%;
  margin: 0 0 0 0;
  overflow: hidden;
}

.contenido-imagen {
  border-radius: 5px solid #FFFFFF;
  padding: 20px;
