@media (min-width: 768px) {
  .contenedor-card {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    position: relative;
  }
}
@media (max-width: 768px) {
  .contenedor-card {
    display: grid;
    gap: 5rem 2rem;
  }
