.contenedor-card {
  margin-bottom: 5rem;
}

.card {
  background-color: #ce4d9e;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  height: 300px;
  border-radius: 20px;
  box-shadow: 0px 16px 17px -6px rgba(0, 0, 0, 0.33);
  transition: 0.5s;
}
.card .imagen-card {
  position: absolute;
  top: 0;
  width: 100%;
  height: 220px;
  border-radius: 12px;
  transition: 0.5s;
  overflow: hidden;
}
.card .imagen-card img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card .contenido-card {
  position: absolute;
  top: 200px;
  width: 100%;
  padding: 0 3rem;
  height: 3rem;
  text-align: center;
  transition: 0.5s;
}
.card .contenido-card h2 {
  color: #FFFFFF;
  overflow: auto;
}
.card .contenido-card p {
  overflow: hidden;
  height: 1rem;
}
.card:hover {
  height: 500px;
}
.card:hover .card {
  gap: 0 0 8rem 0;
}
.card:hover .imagen-card {
  top: -45px;
  scale: 0.75;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.5);
}
.card:hover .contenido-card {
  top: 150px;
  height: 250px;
}
.card:hover .contenido-card p {
  top: 100px;
  height: -webkit-fill-available;
}

@media (min-width: 768px) {
  .seccion-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
@media (max-width: 768px) {
  .imagen img {
    border-radius: 12px;
    height: 100%;
    width: 100%;
  }
}
.entrada-blog {
  position: relative;
  height: auto;
  border-radius: 20px;
  margin-bottom: 2rem;
}
.entrada-blog p {
  height: -webkit-fill-available;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
  .entrada-blog .imagen img {
    border-radius: 12px;
    height: 100%;
    left: 0;
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    width: 48%;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada {
