.prueba {
  background: gradient(355deg, #281c1c, rgba(0, 0, 0, 0.714));
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
}

.carrusel {
  background-color: red;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* Ajusta el ancho del contenedor al 100% del elemento padre */
  overflow: hidden; /* Oculta el contenido que se desborda */
}

.carrusel-items {
  display: flex;
  overflow-x: scroll;
  gap: 2rem;
}

.carrusel-item {
  min-width: 300px;
  max-height: 300px;
}
.carrusel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-prueba {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 5rem;
  z-index: 10;
}

.nav li {
  list-style: none;
  margin: 0 1rem;
}

.buscar {
  position: relative;
  width: 30rem;
  height: 4rem;
}

.buscar input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: #FFFFFF;
  background: transparent;
  border: 1 solid rgba(255, 255, 255, 0.5);
  outline: none;
  border-radius: 4px;
  padding: 0 10px 0 45px !important;
  backdrop-filter: blur(10px);
}

.buscar input::placeholder {
  color: #FFFFFF;
}

.buscar i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  padding-right: 10px;
  color: #FFFFFF;
  border-right: 1px solid #FFFFFF;
}

@media (min-width: 768px) {
  .banner {
    display: block;
    column-gap: 2rem;
  }
}
.banner {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 0 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: url("../img/movies/bg-little-mermaid.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  transition: 0.5s;
}

.banner::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

@media (max-width: 768px) {
  .banner {
    display: flex; /* Activar Flexbox */
    flex-wrap: wrap;
    justify-content: center; /* Centrar horizontalmente */
    align-items: center; /* Centrar verticalmente */
    transform: scale(1); /* Escala del contenido */
    transition: 0.5s; /* Transici√≥n suave */
    visibility: visible;
    width: 100vw; /* Ancho completo de la ventana */
  }
  .content .active {
    margin-bottom: 3rem;
    padding: 1rem 1rem;
  }
  .swiper {
    width: 250px;
    margin: 0;
  }
}
.content {
  position: relative;
  margin-top: 10rem;
  max-width: 550px;
  display: none;
  visibility: hidden;
  transform: scale(0);
  transition: 0.5s;
}

.content.active {
  display: block;
  visibility: visible;
  transform: scale(1);
  transition: 0.5s;
  width: 100vh;
}

.movie-title {
  max-width: 300px;
}

.banner .content h4 {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 400;
  font-size: 30px;
}
.banner .content h4 span {
  padding: 0 10px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.banner .content h4 span:first-child {
  padding-left: 0;
}
.banner .content h4 span:last-child {
  border-right: none;
}
.banner .content h4 span i {
  background: #a03b8a;
  color: #FFFFFF;
  padding: 0 8px;
  display: inline-block;
  border-radius: 2px;
}

.banner .content p {
  font-size: 1em;
  font-weight: 300;
  line-height: 1.5em;
  color: #FFFFFF;
  margin: 10px 0 20px;
}

.banner .content .boton {
  position: relative;
}
.banner .content .boton a {
  position: relative;
  display: inline-block;
  margin-right: 1rem;
  background: #a03b8a;
  color: #FFFFFF;
  padding: 6px 20px;
  border-radius: 20px;
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: 0.5s;
  cursor: pointer;
}
.banner .content .boton a:nth-child(2) {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.banner .content .boton a:nth-child(2):hover {
  background: #a03b8a;
}

.swiper {
  display: flex !important;
  width: 350px;
  padding-top: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 350px;
}

.swiper-slide img {
  display: block;
  width: 100%;
  -webkit-box-reflect: below 1px linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.1333333333), rgba(0, 0, 0, 0.2666666667));
}